import './App.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSpinner } from '@fortawesome/free-solid-svg-icons'
import { useState, useEffect } from 'react';


function App() {
  
    const [info, setInfo] = useState(false);

    // Here the useEffect starts to "load" users after component is first rendered
    useEffect(() => {
        fetch('https://jsonplaceholder.typicode.com/users?_delay=5000&t='+Math.random())
            .then(response => response.json())
            .then(result => setInfo(result))
    }, []);

    return (
        <div className="App">
            Info
            {info ? 
                <ul>
                    {info.map(user => <li>{user.name}</li>)}
                </ul>
            :
            <div className="spinner" >
            <FontAwesomeIcon con={faSpinner} />
         </div>
            }
        </div>
    );
}

export default App;